import React from "react"
import styled from "styled-components"
import ModalImage from "react-modal-image"

const StyledGrid = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(270px, 270px));
  grid-gap: 15px;
  justify-content: center;
  > div {
    margin: auto;
    background-color: rgb(66, 69, 66, 0.4);
    margin: 5px;
    border: 2px solid white;
    height: 270px;
    border-radius: 5px;
    text-align: center;
  }
`

const Header = styled.div`
  color: white;
  font-size: 60px;
  font-family: "Poiret One", cursive;
  font-weight: 500;
  text-align: center;
  cursor: default;
  text-shadow: 2px 2px 2px black;
  margin-bottom: 10px;
`
const StyledModalImage = styled(ModalImage)`
  margin: 0;
`

const EmptyView = styled.h2`
  margin: auto;
  color: white;
  cursor: default;
  font-family: "Poiret One", cursive;
  font-size: 50px;
  font-weight: 600;
`

const Gallery = ({ photos, path, pageName }) => {
  return photos && photos.edges.length > 0 ? (
    <div style={{ width: "100%" }}>
      <Header>{pageName}</Header>
      <StyledGrid>
        {photos.edges.map((photo, index) => (
          <StyledModalImage
            key={index}
            hideDownload="true"
            hideZoom="true"
            small={photo.node.childImageSharp.small.src}
            large={photo.node.childImageSharp.large.src}
          />
        ))}
      </StyledGrid>
    </div>
  ) : (
    <EmptyView>Nothing here</EmptyView>
  )
}

export default Gallery
