import React from "react"
import styled from "styled-components"

const MainDescription = styled.div`
  font-family: "Poiret One", cursive;
  text-shadow: 2px 2px 2px black;
  color: white;
  font-size: 3.2em;
  max-width: 800px;
  font-weight: 500;
  cursor: default;
`
const MainContent = () => {
  return (
    <MainDescription>
      Precyzyjne wycinanie laserowe nawet najbardziej skomplikowanych projektów.
    </MainDescription>
  )
}

export default MainContent
