import { graphql, useStaticQuery } from "gatsby"
import React, { useState } from "react"
import styled from "styled-components"
import Contact from "./kontakt"
import Logo from "../components/atoms/Logo"
import LogoText from "../components/atoms/LogoText"
import Toggle from "../components/atoms/Toggle"
import Menu from "../components/molecules/Menu"
import MainContent from "../components/molecules/MainContent"
import logoText from "../assets/logo-one-line-text.svg"
import logo from "../assets/logo.svg"
import "bootstrap/dist/css/bootstrap.min.css"
import "../index.css"
import Gallery from "../components/organisms/Gallery"

const Header = styled.header`
  position: fixed;
  width: 100%;
  height: 60px;
  align-items: center;
  padding: 0 100px;
  display: flex;
  justify-content: space-between;
  background: white;
  z-index: 1000;

  @media (max-width: 1064px) {
    padding: 0 10px;
    &.active {
      > div:before {
        top: 14px;
        transform: rotate(45deg);
        box-shadow: 0 0 0 #fff;
      }
      > div:after {
        bottom: 14px;
        transform: rotate(-45deg);
      }
    }
  }
`

const StyledSection = styled.section`
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 100vh;
  padding: 150px 100px 100px;

  @media (max-width: 1064px) {
    padding: 100px 50px 50px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    display: flex;
  }
`

const ContactSection = styled.section`
  background: white;
  padding-left: 20px;

  @media (max-width: 940px) {
    padding-left: 4px;
    padding-right: 4px;
  }
`

const Home = () => {
  const [activeMenu, setActiveMenu] = useState(false)

  const toggle = () => {
    setActiveMenu(x => !x)
  }

  const photos = useStaticQuery(graphql`
    {
      allFile(filter: { extension: { in: ["png", "jpg", "jpeg"] } }) {
        group(field: relativeDirectory) {
          relativeDirectory: fieldValue
          totalCount
          edges {
            node {
              relativePath
              publicURL
              absolutePath
              childImageSharp {
                small: fixed(height: 270) {
                  ...GatsbyImageSharpFixed
                }
                large: fixed(height: 600) {
                  ...GatsbyImageSharpFixed
                }
              }
            }
          }
        }
      }
    }
  `)
  console.log(photos)
  return (
    <>
      <Header className={activeMenu ? "active" : ""} id="header">
        <LogoText src={logoText} alt="Firma z drewna" to="/" />
        <Menu callback={() => toggle()} activeMenu={activeMenu} />
        <Toggle callback={() => toggle()} />
      </Header>
      <StyledSection id="home">
        <MainContent />
        <Logo src={logo} alt="Logo" />
      </StyledSection>
      <StyledSection id="laser">
        <Gallery
          photos={photos.allFile.group.find(
            g => g.relativeDirectory === "laser"
          )}
          pageName="Laser"
        />
      </StyledSection>
      <StyledSection id="gardenArchitecture">
        <Gallery
          photos={photos.allFile.group.find(
            g => g.relativeDirectory === "gardenArchitecture"
          )}
          pageName="Architektura ogrodowa"
        />
      </StyledSection>
      <StyledSection id="decorations">
        <Gallery
          photos={photos.allFile.group.find(
            g => g.relativeDirectory === "decorations"
          )}
          pageName="Dekoracje"
        />
      </StyledSection>
      <ContactSection id="contact">
        <Contact />
      </ContactSection>
    </>
  )
}

export default Home
