import React, { useEffect } from "react"
import styled from "styled-components"
import { Link } from "gatsby"

const StyledWrapper = styled.ul`
  position: relative;
  display: flex;
  margin-bottom: 0;

  @media (max-width: 1064px) {
    position: absolute;
    margin-left: 0;
    top: -100%;
    left: 0;
    width: 100%;
    height: calc(100vh - 60px);
    padding: 50px 0px;
    text-align: center;
    overflow: auto;
    transition: 0.5s;
    background: white;
    flex-direction: column;
    opacity: 0;
    transform: translateY(-100%);

    &.active {
      transform: translateY(0);
      opacity: 1;
      top: 59px;
    }
  }
`

const StyledLi = styled.li`
  position: relative;
  list-style: none;
  margin: 5px 0;
`

const StyledLink = styled(Link)`
  position: relative;
  width: 100%;
  padding: 5px 10px;
  font-family: "Poiret One", cursive;
  font-weight: 800;
  font-size: 21px;
  color: black;
  display: inline-block;
  :hover,
  &.active {
    text-decoration: none;
    color: rgb(132, 194, 37);
  }

  @media (max-width: 1064px) {
    font-size: 30px;
    padding: 5px 50px;
    font-weight: 700;
    margin: 10px 0;
  }
`

const Menu = ({ activeMenu, callback }) => {
  useEffect(() => {
    let onscroll = () => {
      var html = document.getElementsByTagName("html")[0]
      var scrollDistance = html.scrollTop + window.innerHeight / 2
      var sections = document.getElementsByTagName("section")
      var navElements = document.getElementsByClassName("navigation")
      Array.from(sections).forEach(section => {
        if (
          section.offsetTop <= scrollDistance ||
          section.offsetTop + section.offsetHeight <=
            html.scrollTop + window.innerHeight
        ) {
          Array.from(navElements).forEach(e => {
            Array.from(e.childNodes)
              .filter(node => node.tagName === "A")
              .forEach(node => {
                node.classList.remove("active")
                if (node.hash === `#${section.id}`) node.classList.add("active")
              })
          })
        }
      })
    }
    window.onscroll = onscroll
    onscroll()
  })

  return (
    <StyledWrapper className={activeMenu ? "active" : ""}>
      <StyledLi className="navigation">
        <StyledLink onClick={callback} to="#home">
          Strona główna
        </StyledLink>
      </StyledLi>
      <StyledLi className="navigation">
        <StyledLink onClick={callback} to="#laser">
          Laser
        </StyledLink>
      </StyledLi>
      <StyledLi className="navigation">
        <StyledLink onClick={callback} to="#gardenArchitecture">
          Architektura ogrodowa
        </StyledLink>
      </StyledLi>
      <StyledLi className="navigation">
        <StyledLink onClick={callback} to="#decorations">
          Dekoracje
        </StyledLink>
      </StyledLi>
      <StyledLi className="navigation">
        <StyledLink onClick={callback} to="#contact">
          Kontakt
        </StyledLink>
      </StyledLi>
    </StyledWrapper>
  )
}

export default Menu
